export default {
	name:"product-list",//商品内容
	props:{
		opction:{
			type:Object,
			default:{}
		}
	},
	data() {
		return {
			imageHeight:0,//图片高度
		};
	},
	mounted() {
		let query = uni.createSelectorQuery().in(this)
		query.select('.image').boundingClientRect().exec(doc=>{
			this.imageHeight = doc[0].width-8
		})
	},
	methods:{
		toDetail(){
			uni.navigateTo({
				url:"../../pages/detail/detail?pid="+this.opction.pid
			})
		}
	}
}
